@import './public/styles/main-styles.scss';

.card {
  display: flex;
  position: relative;
  flex-direction: column;
  width: 100%;
  border-radius: 15px;
  overflow: hidden;
  cursor: pointer;
  transition: transform ease-in 0.2s;
  height: 350px;
  max-width: 250px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.07);


  &__img {
    position: relative;
    overflow: hidden;
    transition: transform ease-in 0.2s;
    max-height: 225px;
    height: 100%;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  &__title {
    margin-bottom: 5px;
  }

  &__price {
    transition: opacity ease-in 0.3s;
    opacity: 1;
  }

  &__info {
    transition: 0.3s;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__button {
    cursor: pointer;
    outline: none;
    opacity: 1;
    transition: 0.3s;

    &:hover {
      background-color: rgba(186, 176, 176, 0.8);;
    }
  }

  &__button-container {
    position: absolute;
    display: grid;
    grid-auto-flow: column;
    grid-gap: 16px;
    bottom: -60px;
    left: 20px;
    transition: 0.3s;
    padding: 20px 0;
    transform: translate(0, 0);
  }

  &:hover .card__price {
    opacity: 1;
  }

  &:hover .card__button {
    opacity: 1;
    transform: translate(0, -60px);
  }

  &:hover .card__info {
    transform: translate(0, -20px);
  }

  &:hover .card__img {
    transform: translate(0, -10px);
  }

}
